.loader {
  color: #000;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: 400;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  height: 4px;
  width: 0%;
  background: #ff3d00;
  animation: lineGrow 5s linear infinite;
  display: block;
}

@keyframes lineGrow {
  to {
    width: 100%;
  }
}

.cat-info {
  gap: 50px;
  padding: 50px;
  display: flex;
}

.cat-info img {
  width: 50%;
}

.cat-card h2 {
  font-size: 60px;
}

.cat-card p {
  font-size: 24px;
}

